.App {
  background-image: linear-gradient(#111111, #222222);
  height: 100%;
}
.names-box {
  background-color: rgba(255, 255, 255, 0.4);
  padding: 0px;
  border: 0px;
  margin: 0px;
  height: 60vh;
  font-size: 16px;
  resize: none;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.name-input {
  background-color: rgba(0, 0, 0, 0.1);
  width: 18vw;
  padding: 20px;
  height: 95%;
  margin-right: 20px;
  float: left;
}

.form-header {
  color: white;
  font-weight: 300;
  text-align: center;
  font-size: 24px;
  user-select: none;
}
.file-upload {
  width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.file-upload + label {
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  margin: 10px 20px 0px 0px;
  width: 95%;
  color: white;
  padding: 10px;
  border-radius: 35px;
  background-color: rgb(17, 146, 86);
  display: inline-block;
  font-family: 'Segoe UI Light', Tahoma, Geneva, Verdana, sans-serif;
  transition: all 0.5s;
  user-select: none;
}

.file-upload:focus + label,
.file-upload + label:hover {
  cursor: pointer;
  background-color: rgb(38, 165, 80);
}

.form-frame {
  margin: 20px 20px 0px 20px;
  padding: 30px 0px 30px 30px;
  float: left;
  background-color: rgba(0,0,0,.1);
  width: 44vw;
  height: 90vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.form-frame::-webkit-scrollbar {
  display: none;
}

.output-box {
  margin: 20px 20px 0px 20px;
  padding: 30px 0px 30px 30px;
  float: left;
  background-color: rgba(0,0,0,.1);
  width: 38vw;
  height: 90vh;
  overflow-y: scroll;
}

.name-list {
  margin-top: 40px;
  background-color: rgba(255, 255, 255, 0.5);
  max-height: 60vh;
  overflow-y: scroll;
  overflow-x: hidden;
}
.names {
  font-size:14px;
  margin:6px;
  user-select: none;
}

.error-text {
  color: rgb(156, 18, 18);
}

.info-text {
  font-size: 20px;
  color: rgb(122, 216, 122);
}

.pair-display {
  border: solid 1px black;
  border-collapse: collapse;
  background-color: rgba(255, 255, 255, 0.5);
}

th, td {
  border: solid 1px black;
  font-size: 14px;
  padding: 0px 10px 0px 10px;
  vertical-align: center;
}

.result-header {
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 300;
  user-select: none;
}

html {
  background-color: #5089ca;
  background-image:
  repeating-linear-gradient(120deg, rgba(255,255,255,.1), rgba(255,255,255,.1) 1px, transparent 1px, transparent 60px),
  repeating-linear-gradient(60deg, rgba(255,255,255,.1), rgba(255,255,255,.1) 1px, transparent 1px, transparent 60px),
  linear-gradient(60deg, rgba(0,0,0,.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,.1) 75%, rgba(0,0,0,.1)),
  linear-gradient(120deg, rgba(0,0,0,.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,.1) 75%, rgba(0,0,0,.1));
  background-size: 70px 120px;
}

::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}

.submit-button {
  
  outline:none;
  border:none;
  padding:0.25em 2.75em 0.25em 0.6em;
  text-align:left;
  background:
    linear-gradient(-120deg, transparent 1em, #0099C3 1.05em , #0099C3 1.5em, transparent 1.45em, transparent 2em, #0099C3 2.05em) top no-repeat,
    linear-gradient(300deg, transparent 1em, #0099C3 1.05em , #0099C3 1.5em, transparent 1.45em, transparent 2em, #0099C3 2.05em) bottom no-repeat ;
  background-size: 100% 50%;
  color:white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 24px;
  float: left;
  position: absolute;
  top: 50%;
  user-select: none;
}
.submit-button:hover {
  background:
    linear-gradient(-120deg, transparent 1em, rgb(7, 131, 165) 1.05em , rgb(7, 131, 165) 1.5em, transparent 1.45em, transparent 2em, rgb(7, 131, 165) 2.05em) top no-repeat,
    linear-gradient(300deg, transparent 1em, rgb(7, 131, 165) 1.05em , rgb(7, 131, 165) 1.5em, transparent 1.45em, transparent 2em, rgb(7, 131, 165) 2.05em) bottom no-repeat ;
    background-size: 100% 50%;
  cursor: pointer;
}

.submit-column {
  width: 9vw;
  height: 100vh;
  background-color: transparent;
  float: left;
}